export default {
  namespaced: true,
  state: {
    stackMaximum: 20,
    stack: [],
    current: -1
  },
  getters: {
    // StackMaxiumum
    stackOverFlow: state => {
      return state.stack.length >= state.stackMaximum;
    },
    // Event
    getCurrentEvent: state => {
      return state.stack[state.current];
    }
  },
  mutations: {
    /**
     * 
     * @param {Object} state Store State
     * @param {Object} event event Object
     */
    pushEvent(state, event) {
      state.stack.push(event);
      console.log("eventPush:", event);
    },
    /**
     * 
     * @param {Object} state Store State
     */
    shiftEventStack(state) {
      state.stack.shift();
    },
    /**
     * 
     * @param {Object} state Store State
     */
    backPointer(state) {
      state.current--;
    },
    /**
     * 
     * @param {Object} state Store State
     */
    forwardPointer(state) {
      state.current++;
    },
    /**
     * CurrentStack
     * @param {Object} state Store State
     */
    spliceStack(state) {
      state.stack.splice(state.current);
    },
    /**
     * Event
     * @param {Object} state Store State
     * @param {Number} index index
     */
    spliceStackEvent(state, index) {
      state.stack.splice(index, 1);
    },
    resetStack(state) {
      state.stack = [];
      state.current = -1;
    }
  },
  actions: {
    /**
     * 
     * @param {Object} Store Store
     * @param {Object} event event Object
     * @param {String} event.type 
     * @param {String} event.id ID
     */
    eventProcess({ getters, commit, state }, event) {
      // 
      if (getters.stackOverFlow) {
        commit("shiftEventStack");
      }
      // 
      if (state.current < state.stackMaximum - 1) {
        // Poniter
        commit("forwardPointer");
      }
      // Stack
      commit("spliceStack");
      commit("pushEvent", event);
    },
    /**
     * 
     * @param {Object} Store Store Object
     */
    undo({ commit, rootGetters, state }) {
      // 
      let event = state.stack[state.current];
      console.log("UNDO:", event);
      // type
      switch (event.type) {
        case "add": {
          let index = rootGetters["componentStore/getComponentIndexByID"](
            event.id
          );
          commit("componentStore/spliceComponentByIndex", index, {
            root: true
          });
          commit("componentStore/hideStyleModifacationCard", null, {
            root: true
          });
          commit("backPointer");
          break;
        }
        case "move": {
          commit("componentStore/componentExchange", event, { root: true });
          commit("backPointer");
          break;
        }
        case "update": {
          let payload = {
            id: event.id,
            componentInfo: event.oldComponentInfo
          };
          commit("componentStore/replaceComponentInfoByID", payload, {
            root: true
          });
          commit("backPointer");
          break;
        }
        case "remove": {
          let payload = {
            index: event.index,
            component: event.component
          };
          commit("componentStore/addComponentByIndex", payload, {
            root: true
          });
          commit("backPointer");
          break;
        }
      }
    },
    /**
     * 
     * @param {Object} Store Store Object
     */
    redo({ commit, state }) {
      // RedoEvent
      let event = state.stack[state.current + 1];
      switch (event.type) {
        case "add": {
          commit("componentStore/pushComponent", event.component, {
            root: true
          });
          commit("forwardPointer");
          break;
        }
        case "move": {
          commit("componentStore/componentExchange", event, { root: true });
          commit("forwardPointer");
          break;
        }
        case "update": {
          let payload = {
            id: event.id,
            componentInfo: event.newComponentInfo
          };
          commit("componentStore/replaceComponentInfoByID", payload, {
            root: true
          });
          commit("forwardPointer");
          break;
        }
        case "remove": {
          commit("componentStore/spliceComponentByIndex", event.index, {
            root: true
          });
          commit("forwardPointer");
          break;
        }
      }
    }
  }
};
