export default {
  namespaced: true,
  state: {
    // 
    activeMenuIndex: null,
    // 
    rateValue: 100,
    // 
    pagePreview: false
  },
  getters: {
    // 
    showDisplayCard: state => {
      return state.activeMenuIndex !== null;
    },
    // Item
    showComponents: ({ state, getters }) => {
      return getters.showDisplayCard && state.activeMenuIndex === 0;
    }
  },
  mutations: {
    /**
     * 
     * @param {Object} state Store state
     */
    showPagePreview(state) {
      state.pagePreview = true;
    },
    /**
     * 
     * @param {Object} state Store state
     */
    hidePagePreview(state) {
      state.pagePreview = false;
    },
    /**
     * 
     * @param {Object} state Store state
     * @param {Number} val 
     */
    updateRateValue(state, val) {
      state.rateValue = val;
    },
    /**
     * item
     * @param {Object} state Store state
     * @param {Number} index index
     */
    changeActiveMenu(state, index) {
      // 
      if (state.activeMenuIndex === index) {
        state.activeMenuIndex = null;
      } else {
        state.activeMenuIndex = index;
      }
    }
  }
};
